/**/

$modifier-prefix: --;
$element-prefix: __;
$default-side-margin: no;
$size-divider: -;

/**/
$base-text-size-px: 13;
$base-text-size: $($base-text-size-px)px;

/**/
$base-text-color: #333;

/**/
$base-link-color: #074ae2;

/**/

@font-face {
  font-family: "MyYuGothicM";
  font-weight: normal;
  src: local("YuGothic-Medium"), /* Postscript name */
  local("Yu Gothic Medium"), /* for Chrome */
  local("YuGothic-Regular"); /* Medium */
}

@font-face {
  font-family: "MyYuGothicM";
  font-weight: bold;
  src: local("YuGothic-Bold"), /* Postscript name */
  local("Yu Gothic"); /* for Chrome */
}

$base-font-family:
  'Noto Sans CJK JP',
  'Noto Sans CJK Japanese',
  MyYuGothicM, /* Windows  */
  YuGothic, /* OS X  */
  -apple-system, /* OS X, iOS San Francisco */
  BlinkMacSystemFont, /* OS X, iOS Chrome San Francisco */
  "Hiragino Kaku Gothic ProN", /* OS X, iOS  */
  Meiryo, /* Windows  */
  sans-serif;

/**/
$gaps: 0, 10, 20;

/**/
$screen-size:
  960px,
  960px - 1,
  720px,
  720px - 1,
  640px,
  480px,
  480px - 1;

/**/
$screen-size-label:
  pc,
  pc-max,
  tablet,
  tablet-max,
  breakpoint-1,
  mobile,
  mobile-max;
