/*  */
@media (max-width: map-get($screen-size, tablet)) {

  .data {
    overflow-y: hidden;
    overflow-x: auto;
  }
}

/*  */
@media (max-width: map-get($screen-size, mobile)) {

  html {
    // font-size: 0.875em;//Mobile14px
    //OFF
    -webkit-text-size-adjust: none;
  }

  /*  */
  img {
    max-width: 100%;
    height: auto;
  }

  /*  */
  table {
    overflow: auto;
  }

}
